import './App.css';
import { useState } from 'react';


function App() {
  const [total,setTotal]=useState(0)

  useState(()=>{
    getApi()


  },[1])


  function getApi(){
    var myHeaders = new Headers();
myHeaders.append("Cookie", "__cfduid=dbb6d7cc7f4a720d83e833674fb3e7f2b1614244379");

var requestOptions = {
  method: 'GET',
  headers: myHeaders,
  redirect: 'follow'
};

fetch("https://gab.goagrimyanmar.com/data.php", requestOptions)
  .then(response => response.json())
  .then(result => {
    setTotal(result.length)
    reload()
  })
  .catch(error => console.log('error', error));
  }


  function reload(){
    setTimeout(function(){ getApi() }, 3000);
  }
  return (
    <div className="App">
      <header className="App-header">
        <p>Current Report </p>
        <p style={{fontSize:"50px"}}>{total}</p>
      </header>
    </div>
  );
}

export default App;
